import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';

const useStyles = makeStyles({
    styleLetterBlack: {
        fontFamily: "Roboto Black",
        whiteSpace: 'initial'
    },
    styleLetterLight: {
        fontFamily: "Roboto Light",
    },
    scroll: {
        overflowY: 'auto'
    }
});



export const Data01 = () => {
    const classes = useStyles();


    return (
        <div className="card-chemistry">
            <Card elevation={4}>
                <CardActionArea>
                    <CardMedia
                        component="img"
                        height="190"
                        image="https://i.ibb.co/KFcg9Ft/IMG-20210709-1.jpg"
                        alt="Imagen de quimica"
                    />
                    <CardContent className={classes.scroll}>
                        <p
                            className={classes.styleLetterBlack}
                        >
                            Material de vidrio y cristalera
                        </p>
                        <div className="description-card">
                            El material de vidrio que se tiene en el laboratorio de Qumica y
                            Nanotecnologa se utiliza para llevar a cabo las prcticas de
                            laboratorio y las investigaciones que se realizan por el cuerpo
                            acadmico. Dentro de los principales materiales de vidrio y cristalera
                            como podemos apreciar en las imgenes son, Matraces Erlenmeyer,
                            embudos de separacin, matraces bola de fondo plano, matraces
                            aforados de diferentes capacidades, vasos de precipitado, cpsulas
                            de porcelana, probetas, pipetas serolgicas y volumtricas, tubos de
                            ensayo entre otros materiales.
                        </div>
                    </CardContent>
                </CardActionArea>
            </Card>
        </div>
    )
}


